import Pricingbox from "./Pricingbox"

export default function Plan() {
    return (
        <>
            <section className="plan px-sm-5 px-lg-0 py-5">
                <div className="container px-sm-5 px-lg-0 py-5">
                    <div className="title line-middle text-center">
                        <h2>Our Special Plan</h2>
                    </div>
                    <div className="pricing-box row">
                        <Pricingbox plan='ONETIME PLAN' price='$70'/>
                        <Pricingbox plan='REGULAR PLAN' price='$80'/>
                        <Pricingbox plan='DEEP PLAN' price='$90'/>
                    </div>
                </div>
            </section>
        </>
    )
}
