
import { Link } from "react-router-dom";

export default function Breadcrum({title}) {
    return (
        <>
             <section className='breadcrum py-5 px-sm-5 px-lg-0'>
                <div className="container py-5 px-sm-5 px-lg-0">


                    <div className="title">
                        <div className="path">
                            <Link to='/home'>Home</Link> <i class="fas fa-angle-right"></i>{title}
                        </div>
                        <h1>{title}</h1>
                    </div>
                </div>
            </section>
        </>
    )
}
