import { useState, useEffect } from "react";
import Projectprops from "./Projectprops";
import img1 from '../assets/work_1.jpg';

const img = [
    { id: 1, name: 'work_1.86732381.jpg', tag: 'development', heading: ' Digital Solutions', p: 'Strategy ' },
    { id: 2, name: 'work_2.01b3bd69.jpg', tag: 'development', heading: ' Creative Ideas ', p: 'Development' },
    { id: 4, name: 'work_4.4a3fc676.jpg', tag: 'communication', heading: ' Marketing Policy', p: 'Creative ' },
    { id: 5, name: 'work_5.b0490a24.jpg', tag: 'marketing', heading: ' Creative Strategy', p: 'Marketing' },
    { id: 6, name: 'work_6.52b0efdf.jpg', tag: 'marketing', heading: ' Marketing Strategy', p: 'Social' },
    { id: 7, name: 'work_7.f11c365f.jpg', tag: 'communication', heading: ' Campaign & PR', p: 'Design' },
    { id: 8, name: 'work_8.ff3c6a99.jpg', tag: 'digital', heading: 'Digital Service ', p: 'Media' }
]

export default function Project() {
    const [tag, setTag] = useState('all');
    const [filtered, setFiltered] = useState([]);

    useEffect(() => {
        tag === 'all' ? setFiltered(img) : setFiltered(img.filter(image => image.tag === tag))
    }, [tag])

    const fliter = (cat) => {
        setTag(cat)
    }


    return (
        <>
            <section className="project-section  px-sm-5 px-lg-0">
                <div className="container  px-sm-5 px-lg-0">
                    <div className="title text-center">
                        <h2>Latest Project</h2>
                    </div>

                    <div className="isotope">
                        <div className="filter-buttons">
                            <button onClick={() => fliter('all')} >All Projects</button>
                            <button onClick={() => fliter('marketing')} >Marketing</button>
                            <button onClick={() => fliter('development')}>Development</button>
                            <button onClick={() => fliter('digital')}>Digital</button>
                            <button onClick={() => fliter('communication')}>Communication</button>
                        </div>
                        <div className="img-container row">{filtered.map(image =>
                            <Projectprops image={image} imgsrc={img1} />
                            )}</div>
                    </div>
                </div>
            </section>
        </>
    )


}
