

export default function Testimonialslider({ imgsrc, name, post }) {
    return (
        <>
            <div className="testimonial-box">
                <div className="img-wrapper">
                    <img src={imgsrc} alt="" />
                </div>
                <div className="details">
                    <p>
                    <i class="fas fa-quote-left"></i>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ratione iste et corrupti, excepturi eum recusandae beatae accusantium quos. Optio, qui minus placeat quo molestias eum eius voluptatum nesciunt unde earum dolores necessitatibus, officia, illo repellendus! Officiis beatae id quas ea.
                    </p>
                    <div className="name">{name}</div>
                    <div className="post">{post}</div>
                    <div className="review">
                    <i class="fas fa-star"></i><i class="fas fa-star"></i><i class="fas fa-star"></i><i class="fas fa-star"></i><i class="fas fa-star-half-alt"></i>
                    </div>
                </div>
            </div>
        </>
    )
}
