import React from 'react'

export default function Sepcial() {
    return (
        <>
            <section className="special-section px-sm-5 px-lg-0">
                <div className="container  py-5 px-sm-5 px-lg-0">
                    <div className="row px-2">
                        <div className="col-md-3 col-sm-12  my-sm-2 my-md-0 ">
                            <h2>What Makes<br />
                                us Special?</h2>
                        </div>
                        <div className="col-md-3 col-sm-12 my-sm-2 my-md-0  d-flex">
                            <div className="left">
                                <h2>275</h2>
                                <p>Loggs transfered</p>

                            </div>
                            <i class="fab fa-react"></i>
                        </div>
                        <div className="col-md-3 col-sm-12  my-sm-2 my-md-0 d-flex">
                            <div className="left">
                                <h2>374</h2>
                                <p>Happy customers</p>

                            </div>
                            <i class="far fa-heart"></i>
                        </div>
                        <div className="col-md-3 col-sm-12  my-sm-2 my-md-0 d-flex">
                            <div className="left">
                                <h2>437</h2>
                                <p>Miles travelled</p>

                            </div>
                            <i class="fas fa-arrows-alt"></i>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
