import img1 from '../assets/brand1.jpg';
import img2 from '../assets/brand2.jpg';
import img3 from '../assets/brand3.jpg';
import img4 from '../assets/brand4.jpg';
import img5 from '../assets/brand5.jpg';
import Brandbox from './Brandbox';
import { Splide, SplideSlide } from '@splidejs/react-splide';

export default function Brand() {
    const option = {
        type:'loop',
        pagination: false,
        perPage: 6,
        perMove: 1,
        arrows: false,
        autoplay: true,
        interval: 1000,
        pauseOnHover: true,
        rewind: true,
        speed: 400,
        gap: '1.5rem',
        breakpoints: {
            992: {
                perPage: 5,
            },
            768: {
                perPage: 4,
            }
        }
    }
    return (
        <>
            <section className="brand px-sm-5 px-lg-0 py-5">
                <div className="container px-sm-5 px-lg-0 py-5">
                    <Splide options={option}>
                        <SplideSlide>
                            <Brandbox imgsrc={img1}/>
                        </SplideSlide>
                        <SplideSlide>
                            <Brandbox imgsrc={img2}/>
                        </SplideSlide>
                        <SplideSlide>
                            <Brandbox imgsrc={img3}/>
                        </SplideSlide>
                        <SplideSlide>
                            <Brandbox imgsrc={img4}/>
                        </SplideSlide>
                        <SplideSlide>
                            <Brandbox imgsrc={img5}/>
                        </SplideSlide>
                        <SplideSlide>
                            <Brandbox imgsrc={img2}/>
                        </SplideSlide>
                    </Splide>
                </div>
            </section>

        </>
    )
}
