

export default function Pricingbox({ plan, price }) {
    return (
        <>
            <div className="col-md-4 col-sm-12 text-center">
                <div className="plan ">
                    <h4>{plan}</h4>
                    <h2>{price}</h2>
                    <p>/Per Month</p>
                </div>
                <div className="plan-desc">
                    <ul >
                        <li><i class="far fa-check-circle"></i>One-time Cleaning</li>
                        <li><i class="far fa-check-circle"></i>Additional trucks</li>
                        <li><i class="far fa-check-circle"></i>Regular Cleaning</li>
                        <li><i class="far fa-check-circle"></i>Move-ins / Outs</li>
                    </ul>
                    <div className="btn">order now</div>
                </div>
            </div>

        </>
    )
}
