import React from 'react';
import logo from '../assets/nav-logo.png';
import {Link} from 'react-router-dom';

export default function Navbar() {
    return (
        < >
            <div className="top-nav py-2">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8 col-md-4 col-sm-12">
                            <div className="contact-info">
                                <i class="fas fa-phone"></i>
                                <p>247 3567845</p> 
                                <i class="far fa-envelope"></i>
                                <p>uemail@gmail.com</p> 
                            </div>

                        </div>
                        <div className="col-lg-2 col-md-4 col-sm-12 ">
                            <ul className="header_social align-middle">
                                <Link to=""><i className="fab fa-facebook-f  "></i></Link>
                                <Link to=""><i className="fab fa-twitter"></i></Link>
                                <Link to=""><i className="fab fa-instagram"></i></Link>
                                <Link to=""><i className="fab fa-pinterest"></i></Link>
                            </ul></div>
                        <div className="col-lg-2 col-md-4 col-sm-12 d-none d-md-block">
                            <Link to='#' className="btn">get a quote</Link>
                        </div>
                    </div>
                </div>
            </div>
            <nav class="navbar navbar-expand-lg navbar-light sticky-top">
                <div class="container">
                    <Link class="navbar-brand" to="/elizah/home"><img src={logo} alt="" /></Link>
                    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-icon"></span>
                    </button>
                    <div class="collapse navbar-collapse" id="navbarNav">
                        <ul class="navbar-nav ms-auto">
                            <li class="nav-item">
                                <Link class="nav-link active" aria-current="page" to="/elizah/home">Home</Link>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" to="/elizah/about">About</Link>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" to="/elizah/service">Services </Link>
                                <div className="dropdown">
                                    <div className="dropdown-item"><Link to="#">Bussiness Growth</Link></div>
                                    <div className="dropdown-item"><Link to="#">Advance Analtic</Link></div>
                                    <div className="dropdown-item"><Link to="#">Express Delivery</Link></div>
                                </div>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" to="/elizah/gallery">Gallery</Link>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" to="/elizah/blog">Blog </Link>
                                <div className="dropdown">
                                    <div className="dropdown-item"><Link to="#">Blog Page</Link></div>
                                    <div className="dropdown-item"><Link to="#">Single Blog</Link></div>
                                </div>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" to='/elizah/team'>Team</Link>
                            </li>
                            <li class="nav-item">
                                <Link class="nav-link" to="/elizah/contact">Contact</Link>
                            </li>
                        </ul>

                    </div>
                </div>
            </nav>
        </>
    )
}
