
export default function Contact() {
    const style = {
        height: '155px',
    }
    return (
        <>
            <section className="contact-us py-5 px-sm-5 px-lg-0">
                <div className="container py-5 px-sm-5 px-lg-0">
                    <div className="title text-center line-middle">
                        <h2>Contact Us</h2>
                    </div>
                    <div className="row">

                        <div className="col-md-4 col-sm-12 d-flex  info">
                            <i class="fas fa-map-marker-alt"></i>
                            <div className="details">
                                <p>Company Name</p>
                                <h2>California, USA</h2>
                            </div>

                        </div>

                        <div className="col-md-4 col-sm-12 d-flex info">
                            <i class="fas fa-phone-alt"></i>
                            <div className="details">
                                <p>Toll Free Number</p>
                                <h2>0 (245) 378-6748</h2>
                            </div>

                        </div>

                        <div className="col-md-4 col-sm-12 d-flex info">
                            <i class="far fa-clock"></i>
                            <div className="details">
                                <p>Opening Hours</p>
                                <h2>Mon-Fri 9am - 6pm</h2>
                            </div>
                        </div>
                    </div>


                    <form className="row my-5">
                        <div className="col-md-6 col-sm-12">

                            <input type="text" className='w-100' placeholder='Full Name' />
                            <input type="email" className='w-100' placeholder='Email'/>
                            <input type="text" className='w-100' placeholder='Number' />
                        </div>
                        <div className="col-md-6 col-sm-12">
                            <div class="form-floating">
                                <textarea class="form-control" placeholder="Leave a comment here" id="floatingTextarea2" style={style}></textarea>
                                <label for="floatingTextarea2">Message</label>
                            </div>

                            <input type="button" value="SUBMIT" className='btn' />
                        </div>
                        
                    </form>

                </div>
            </section>
        </>
    )
}
