import { Splide, SplideSlide } from '@splidejs/react-splide';
import Testimonialslider from './Testimonialslider';
import img1 from '../assets/testimonial1.jpg';
import img2 from '../assets/testimonial2.jpg';
import img3 from '../assets/testimonial3.jpg';

export default function Testimonials() {
    const option = {
        type: 'loop',
        pagination: false,
        perPage: 1,
        arrows: false,
        autoplay: true,
        interval: 5000,
        pauseOnHover: true,
        rewind: true,
        speed: 400,
    }
    return (
        <>
            <section className='testimonials px-sm-5 px-lg-0 py-5'>
                <div className="container  px-sm-5 px-lg-0 py-5">
                    <div className="title text-center line-middle">
                        <h2>What Clients Say</h2>
                    </div>
                    <div className="testimonial-container">
                        <Splide options={option}>
                            <SplideSlide>
                                <Testimonialslider imgsrc={img1} name='John Watson' post='CEO At Facebook ' />
                            </SplideSlide>
                            <SplideSlide>
                                <Testimonialslider imgsrc={img2} name='Deo Crew' post='Manager' />
                            </SplideSlide>
                            <SplideSlide>
                                <Testimonialslider imgsrc={img3} name='Adam Crew' post='Freelancer' />
                            </SplideSlide>
                        </Splide>
                    </div>

                </div>
            </section>
        </>
    )
}
