

export default function Projectprops({image, imgsrc}) {
    return (
        <>
            <div className="img-wrapper col-lg-3 col-md-6 col-sm-12" key={image.id}>
                                <div className="img-img">
                                    <img src={imgsrc} alt={image.name} />
                                    <div className="details">
                                        <div>
                                            <h1>{image.heading}</h1>
                                            <p>{image.p}</p>
                                        </div>
                                        <div className='link-icon'>
                                            <i className="fas fa-link"></i>
                                        </div>
                                    </div>

                                </div>
                            </div>
        </>
    )
}
