import Teamprops from "./Teamprops";
import member1 from '../assets/team-1.jpg';
import member2 from '../assets/team-2.jpg';
import member3 from '../assets/team-3.jpg';
import member4 from '../assets/team-4.jpg';

export default function Meetteam() {
    return (
        <>
            <section className="meet-team py-5 px-sm-5 px-lg-0">
                <div className="container py-5 px-sm-5 px-lg-0">
                    <div className="title text-center line-middle">
                        <h2>Meet The Team</h2>
                    </div>

                    <div className="team-container row py-5">
                        <Teamprops img={member1} name='Adam Gilcrist' position='Sinior Manager' />
                        <Teamprops img={member2} name='Kristen Crew' position='Co Founder' />
                        <Teamprops img={member3} name='Robert Doe' position='Chairman' />
                        <Teamprops img={member4} name='Jon Gilcrist
' position='Sinior Manager' />
                    </div>
                </div>
            </section>
        </>
    )
}
